
body {
  font: 14px/1.4 -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
h1 {
  margin-bottom: 30px;
}
h1 span {
  position: relative;
}
h1 span:after {
  height: 5px;
  right: 0;
  top: -5px;
  left: 0;
  display: block;
  content: '';
  background: #000;
  position: absolute;
}
.prompt {
  padding: 10px;
  width: 300px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 1rem;
}
.prompt:focus {
  border-color: #666
}
.preview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
pre.example {
  background-color: white;
  margin: 0;
  padding: 0;
}



